footer {
  display: flex;
  padding: 2.5em 1.5em;
  background-color: hsl(229, 31%, 21%);
  color: white;
  align-items: center;

  .footer-left-col {
    display: flex;
    list-style: none;
    justify-content: space-around;
    width: 50%;
    padding: 0 6em 0 0;
    .footer-left-list {
      display: flex;
      list-style: none;
      justify-content: space-around;
      width: 100%;
      align-items: center;
      .footer-nav-item {
        text-transform: uppercase;
        font-size: 14px;
        letter-spacing: 0.1em;
        color: white;
        cursor: pointer;

        &:hover {
          color: hsl(0, 94%, 66%);
        }
      }
    }
  }

  .footer-right-col {
    display: flex;
    list-style: none;
    width: 50%;
    .footer-socials {
      display: flex;
      list-style: none;
      justify-content: flex-end;
      width: 100%;
      cursor: pointer;

      .footer-social-link {
        margin: 0 1.5em;
        display: flex;
        justify-content: flex-end;

        .footer-social-item {
          &:hover {
            color: hsl(0, 94%, 66%);
          }
        }
      }
    }
  }
}
